<template>
  <div class="container" @mousedown="mouseDown">
    <h1>Todolist</h1>
    <h3>
      共有<span class="text-primary">{{ lists.length }}</span
      >个任务,其中<span class="text-success">{{ finished.length }}</span
      >项已完成
    </h3>
    <h3>未完成的列表</h3>
    <ul class="list-group">
      <template v-for="(item, index) in lists">
        <li
          class="list-group-item d-flex justify-content-between"
          :key="index"
          v-if="!item.checked"
        >
          <div class="form-group form-check mb-0">
            <input
              type="checkbox"
              class="form-check-input"
              v-model="item.checked"
              @click="() => (item.checked = !item.checked)"
            />
            <!-- 前面的item.isEdit如果是true是编辑的状态非false上面的label不显示显示下面输入框 -->
            <label
              class="form-check-label"
              v-if="!item.isEdit"
              @dblclick="showEdit(item, index)"
              >{{ item.name }}</label
            >
            <label class="form-check-label" :for="'item-' + index" v-else
              ><input type="text" v-model="editValue" ref="myinput"
            /></label>
          </div>
          <button
            type="button"
            class="close"
            aria-label="Close"
            @click="remove(index)"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </li>
      </template>
    </ul>
    <h3>已完成的列表</h3>
    <ul class="list-group">
      <li
        class="list-group-item"
        v-for="(item, index) in finished"
        :key="'finished-' + index"
      >
        <div class="form-group form-check">
          <input
            type="checkbox"
            class="form-check-input"
            :id="'finished-' + index"
            v-model="item.checked"
            disabled
          />
          <label class="form-check-label" :for="'finished-' + index">{{
            item.name
          }}</label>
        </div>
      </li>
    </ul>
    <h3>添加新的Task</h3>
    <div class="form-group">
      <label for="add">添加</label>
      <input
        type="text"
        class="form-control"
        id="add"
        placeholder="添加新的Task"
        v-model="value"
        @keydown.enter="add"
      />
    </div>
    <button type="button" class="btn btn-primary btn-lg btn-block" @click="add">
      确定添加
    </button>
  </div>
</template>
<script>
import { ref, computed, reactive, toRefs, onUpdated } from "vue";
export default {
  name: "Home",
  setup() {
    const myinput = ref(null);
    let editIndex = 0;
    // 1. 加入checkbox -> checked
    // 2. 统计哪些 -> checked ->finish列表
    // 3. add添加item -> item数据结构 -> name,checked,isEdit
    const add = () => {
      state.lists.push({
        name: state.value,
        checked: false,
        isEdit: false,
      });
      state.value = "";
    };
    // 4. 双击进行编辑
    const showEdit = (item, index) => {
      editIndex = index;
      // 进入编辑状态隐藏label 把输入框显示出来
      item.isEdit = true;
      state.editValue - item.name;
    };
    const mouseDown = (e) => {
      console.log("mouseDown->e", e);
      //判断当前的target是否包含input不包含说明点击的位置不是input输入框
      if (myinput.value && e.target !== myinput.value) {
        // 调整数组中的某一个元素
        state.lists[editIndex] = {
          name: state.editValue,
          checked: false,
          isEdit: false,
        };
      }
    };
    // 5. 删除功能 -> 删除特定index的元素
    const remove = (index) => {
      state.lists.splice(index, 1);
    };
    const state = reactive({
      value: "",
      editValue: "",
      lists: [
        {
          name: "1",
          checked: false,
          isEdit: false,
        },
        {
          name: "2",
          checked: false,
          isEdit: false,
        },
        {
          name: "3",
          checked: false,
          isEdit: false,
        },
      ],
      finished: computed(() =>
        state.lists.filter((item) => item.checked == true)
      ),
      // add,
      myinput,
      showEdit,
    });
    onUpdated(() => {
      console.log(myinput.value);
    });
    return { ...toRefs(state), add, mouseDown, remove };
  },
};
</script>
